import apiClient, { getAuthToken } from './apiClient';
import { Client } from '@stomp/stompjs';
import SockJS from 'sockjs-client';

// 서버 기본 URL
const API_BASE_URL = process.env.REACT_APP_API_BASE_URL || 'http://localhost:8080';

// 채팅 메시지 타입 정의
export enum MessageType {
  CHAT = 'CHAT',
  JOIN = 'JOIN',
  LEAVE = 'LEAVE',
  TYPING = 'TYPING',
  READ = 'READ'
}

// 채팅 메시지 인터페이스
export interface ChatMessage {
  id?: string;
  chatRoomId: number;
  senderId: number;
  senderName: string;
  content: string;
  type: MessageType;
  timestamp?: Date | string;
  senderProfileUrl?: string;
}

// STOMP 클라이언트
let stompClient: Client | null = null;
let connectedCallback: () => void = () => {};
let messageCallbacks: Map<number, (message: ChatMessage) => void> = new Map();
let reconnectAttempts = 0;
const MAX_RECONNECT_ATTEMPTS = 10;

// 채팅방 구독 목록
const subscribedRooms: Set<number> = new Set();

/**
 * STOMP 클라이언트 연결
 * @param onConnected 연결 성공 시 콜백
 */
export const connectToChat = (onConnected: () => void = () => {}) => {
  if (stompClient && stompClient.connected) {
    console.log('이미 채팅 서버에 연결되어 있습니다.');
    onConnected();
    return stompClient;
  }

  const token = getAuthToken();
  if (!token) {
    console.error('인증 토큰이 없습니다. 로그인이 필요합니다.');
    return null;
  }

  // 연결 콜백 저장
  connectedCallback = onConnected;

  // 기존 연결이 있다면 먼저 종료
  if (stompClient) {
    try {
      stompClient.deactivate();
      stompClient = null;
    } catch (e) {
      console.error('기존 연결 종료 중 오류:', e);
      stompClient = null;
    }
  }

  console.log('STOMP 클라이언트 연결 시작...');
  
  // STOMP 연결 전 SockJS에 사용될 커스텀 헤더 설정
  const sockJsOptions = {
    transports: ['websocket', 'xhr-streaming', 'xhr-polling'],
    headers: {
      Authorization: `Bearer ${token}`
    }
  };
  
  // 디버깅용 로그: 토큰 처음 5자리만 출력
  console.log(`연결 시도 중... 토큰 접두어: ${token.substring(0, 5)}...`);
  
  // STOMP 클라이언트 생성
  stompClient = new Client({
    webSocketFactory: () => {
      const sockJs = new SockJS(`${API_BASE_URL}/ws`, null, sockJsOptions);
      // SockJS 연결 이벤트 리스너 추가
      sockJs.onopen = () => {
        console.log('SockJS 연결 성공');
      };
      sockJs.onclose = (event: CloseEvent) => {
        console.log(`SockJS 연결 종료: 코드=${event.code}, 이유=${event.reason}`);
      };
      sockJs.onerror = (error: Event) => {
        console.error('SockJS 연결 오류:', error);
      };
      return sockJs;
    },
    connectHeaders: {
      Authorization: `Bearer ${token}`
    },
    debug: function(str) {
      console.log('STOMP Debug: ', str);
    },
    reconnectDelay: 5000, // 연결 끊길 경우 5초 후 재연결
    heartbeatIncoming: 10000, // 서버로부터의 하트비트 간격을 WebSocketConfig에 맞춤
    heartbeatOutgoing: 10000, // 서버로 보내는 하트비트 간격을 WebSocketConfig에 맞춤
    connectionTimeout: 30000, // 연결 타임아웃 30초
  });

  // 연결 성공 이벤트 핸들러
  stompClient.onConnect = (frame) => {
    console.log('채팅 서버에 연결되었습니다:', frame);
    // 연결 성공 시 재연결 시도 횟수 초기화
    reconnectAttempts = 0;
    
    // 기존에 구독했던 채팅방 재구독
    subscribedRooms.forEach(roomId => {
      subscribeToChatRoom(roomId, messageCallbacks.get(roomId) || (() => {}));
    });
    
    // 연결 콜백 호출
    connectedCallback();
  };

  // 에러 이벤트 핸들러
  stompClient.onStompError = (frame) => {
    console.error('STOMP 에러:', frame.headers, frame.body);
    
    reconnectAttempts++;
    
    // 재연결 시도 횟수 제한
    if (reconnectAttempts > MAX_RECONNECT_ATTEMPTS) {
      console.error(`최대 재연결 시도 횟수(${MAX_RECONNECT_ATTEMPTS})를 초과했습니다.`);
      return;
    }
    
    // 5초 후 재연결 시도 (지수 백오프 적용)
    const reconnectDelay = Math.min(5000 * Math.pow(1.5, reconnectAttempts - 1), 30000);
    console.log(`${reconnectDelay}ms 후 재연결 시도 (${reconnectAttempts}/${MAX_RECONNECT_ATTEMPTS})...`);
    
    setTimeout(() => {
      if (stompClient && !stompClient.connected) {
        console.log('연결 오류 발생 후 재연결 시도...');
        try {
          stompClient.deactivate().then(() => {
            stompClient = null;
            connectToChat(connectedCallback);
          });
        } catch (e) {
          console.error('재연결 시도 중 오류:', e);
          stompClient = null;
          connectToChat(connectedCallback);
        }
      }
    }, reconnectDelay);
  };

  // 연결 해제 이벤트 핸들러
  stompClient.onWebSocketClose = (event) => {
    console.log(`WebSocket 연결이 끊겼습니다. 코드: ${event.code}, 이유: ${event.reason}`);
    
    reconnectAttempts++;
    
    // 재연결 시도 횟수 제한
    if (reconnectAttempts > MAX_RECONNECT_ATTEMPTS) {
      console.error(`최대 재연결 시도 횟수(${MAX_RECONNECT_ATTEMPTS})를 초과했습니다.`);
      return;
    }
    
    // 연결 실패 원인에 따라 지연시간 조정
    const reconnectDelay = Math.min(3000 * Math.pow(1.5, reconnectAttempts - 1), 20000);
    console.log(`${reconnectDelay}ms 후 재연결 시도 (${reconnectAttempts}/${MAX_RECONNECT_ATTEMPTS})...`);
    
    // 재연결 시도
    setTimeout(() => {
      if (stompClient && !stompClient.connected) {
        console.log('연결 해제 후 재연결 시도...');
        try {
          stompClient.deactivate().then(() => {
            stompClient = null;
            connectToChat(connectedCallback);
          });
        } catch (e) {
          console.error('재연결 시도 중 오류:', e);
          stompClient = null;
          connectToChat(connectedCallback);
        }
      }
    }, reconnectDelay);
  };

  // 연결 시작
  try {
    stompClient.activate();
  } catch (e) {
    console.error('STOMP 클라이언트 활성화 중 오류:', e);
    stompClient = null;
  }

  return stompClient;
};

/**
 * 채팅 연결 해제
 */
export const disconnectChat = () => {
  if (stompClient && stompClient.connected) {
    stompClient.deactivate();
    console.log('채팅 서버 연결이 종료되었습니다.');
  }
  
  // 구독 목록 초기화
  subscribedRooms.clear();
  messageCallbacks.clear();
};

/**
 * 채팅방 구독
 * @param roomId 채팅방 ID
 * @param onMessageReceived 메시지 수신 콜백
 */
export const subscribeToChatRoom = (
  roomId: number, 
  onMessageReceived: (message: ChatMessage) => void
) => {
  if (!stompClient || !stompClient.connected) {
    console.error('채팅 서버에 연결되어 있지 않습니다.');
    connectToChat(() => subscribeToChatRoom(roomId, onMessageReceived));
    return;
  }

  // 이미 구독 중인지 확인
  if (subscribedRooms.has(roomId)) {
    console.log(`이미 채팅방 ${roomId}에 구독 중입니다.`);
    // 콜백 함수 업데이트
    messageCallbacks.set(roomId, onMessageReceived);
    return;
  }

  // 채팅방 구독
  const subscription = stompClient.subscribe(
    `/topic/chat/${roomId}`, 
    (message) => {
      try {
        const receivedMessage = JSON.parse(message.body) as ChatMessage;
        console.log('채팅 메시지 수신:', receivedMessage);
        onMessageReceived(receivedMessage);
      } catch (error) {
        console.error('메시지 파싱 중 오류:', error);
      }
    }
  );

  // 구독 정보 저장
  subscribedRooms.add(roomId);
  messageCallbacks.set(roomId, onMessageReceived);

  console.log(`채팅방 ${roomId}에 구독했습니다.`);

  return subscription;
};

/**
 * 채팅방 구독 해제
 * @param roomId 채팅방 ID
 */
export const unsubscribeFromChatRoom = (roomId: number) => {
  if (!stompClient || !stompClient.connected) {
    console.error('채팅 서버에 연결되어 있지 않습니다.');
    return;
  }

  // 구독 해제
  subscribedRooms.delete(roomId);
  messageCallbacks.delete(roomId);

  console.log(`채팅방 ${roomId} 구독을 해제했습니다.`);
};

/**
 * 채팅 메시지 전송
 * @param message 전송할 메시지
 */
export const sendChatMessage = (message: ChatMessage) => {
  if (!stompClient || !stompClient.connected) {
    console.error('채팅 서버에 연결되어 있지 않습니다.');
    return Promise.reject('채팅 서버에 연결되어 있지 않습니다.');
  }

  return new Promise<void>((resolve, reject) => {
    try {
      // 메시지 전송 시도 및 재시도 설정
      const maxRetries = 3;
      let currentRetry = 0;
      
      const attemptSend = () => {
        try {
          stompClient!.publish({
            destination: '/app/chat.sendMessage',
            body: JSON.stringify(message),
            headers: {
              'content-type': 'application/json',
              priority: 'high'
            }
          });
          console.log('메시지 전송 성공:', message);
          resolve();
        } catch (error) {
          console.error(`메시지 전송 시도 실패 (${currentRetry + 1}/${maxRetries}):`, error);
          
          if (currentRetry < maxRetries - 1) {
            currentRetry++;
            // 재시도 간격을 점진적으로 증가시킴 (500ms, 1000ms, 2000ms...)
            const retryDelay = Math.min(500 * Math.pow(2, currentRetry), 5000);
            console.log(`${retryDelay}ms 후 메시지 전송 재시도...`);
            setTimeout(attemptSend, retryDelay);
          } else {
            console.error('최대 재시도 횟수 초과, 메시지 전송 실패:', message);
            reject(error);
          }
        }
      };
      
      // 첫 전송 시도
      attemptSend();
    } catch (error) {
      console.error('메시지 전송 중 오류:', error);
      reject(error);
    }
  });
};

/**
 * 사용자 입장 메시지 전송
 * @param chatRoomId 채팅방 ID
 * @param senderId 사용자 ID
 * @param senderName 사용자 이름
 */
export const sendJoinMessage = (chatRoomId: number, senderId: number, senderName: string) => {
  const message: ChatMessage = {
    chatRoomId,
    senderId,
    senderName,
    content: `${senderName}님이 입장했습니다.`,
    type: MessageType.JOIN
  };

  if (!stompClient || !stompClient.connected) {
    console.error('채팅 서버에 연결되어 있지 않습니다.');
    return Promise.reject('채팅 서버에 연결되어 있지 않습니다.');
  }

  return new Promise<void>((resolve, reject) => {
    try {
      stompClient!.publish({
        destination: '/app/chat.addUser',
        body: JSON.stringify(message)
      });
      console.log('입장 메시지 전송 성공:', message);
      resolve();
    } catch (error) {
      console.error('입장 메시지 전송 중 오류:', error);
      reject(error);
    }
  });
};

/**
 * 타이핑 상태 메시지 전송
 * @param chatRoomId 채팅방 ID
 * @param senderId 사용자 ID
 * @param senderName 사용자 이름
 * @param isTyping 타이핑 중인지 여부
 */
export const sendTypingStatus = (
  chatRoomId: number,
  senderId: number,
  senderName: string,
  isTyping: boolean
) => {
  const message: ChatMessage = {
    chatRoomId,
    senderId,
    senderName,
    content: isTyping ? 'typing' : 'stopped',
    type: MessageType.TYPING
  };

  if (!stompClient || !stompClient.connected) {
    console.error('채팅 서버에 연결되어 있지 않습니다.');
    return;
  }

  stompClient!.publish({
    destination: '/app/chat.typing',
    body: JSON.stringify(message)
  });
};

/**
 * 채팅방 메시지 이력 조회
 * @param roomId 채팅방 ID
 * @param page 페이지 번호
 * @param size 페이지 크기
 */
export const getChatHistory = async (roomId: number, page: number = 0, size: number = 20) => {
  try {
    const response = await apiClient.get(`/api/chat/rooms/${roomId}/messages`, {
      params: { page, size }
    });
    return response.data;
  } catch (error) {
    console.error('채팅 이력 조회 중 오류:', error);
    throw error;
  }
}; 